export default {
    methods: {
        toUrl(val) {
            if (val == 'tel') {
                uni.navigateTo({
                    url: './telOrEmail?from=tel'
                })
            } else if (val == 'email') {
                uni.navigateTo({
                    url: './telOrEmail?from=email'
                })
            } else if (val == 'question') {
                uni.navigateTo({
                    url: './questions'
                })
            }
        }
    },
     mounted(){
        this.closePageLoading(); 
    },
}
